"use client";

import { useEffect, useRef } from "react";

type CanvasSceneProps = {
  className?: string;
};

type Star = {
  x: number;
  y: number;
  r: number;
  a: number;
  dir: number;
  tw: number;
};

const createStars = (count: number): Star[] =>
  Array.from({ length: count }, () => ({
    x: Math.random(),
    y: Math.random(),
    r: 0.6 + Math.random() * 1.4,
    a: 0.25 + Math.random() * 0.55,
    dir: Math.random() > 0.5 ? 1 : -1,
    tw: 0.003 + Math.random() * 0.006,
  }));

export default function CanvasScene({ className }: CanvasSceneProps) {
  const canvasRef = useRef<HTMLCanvasElement | null>(null);

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext("2d");
    if (!ctx) return;

    const stars = createStars(120);
    const prefersReduced = window.matchMedia("(prefers-reduced-motion: reduce)");
    let rafId = 0;
    let width = 0;
    let height = 0;
    let lastTime = 0;

    const drawStars = () => {
      for (const star of stars) {
        ctx.fillStyle = `rgba(125, 211, 252, ${star.a})`;
        ctx.beginPath();
        ctx.arc(star.x * width, star.y * height, star.r, 0, Math.PI * 2);
        ctx.fill();
      }
    };

    const render = (time: number) => {
      if (prefersReduced.matches && time !== 0) return;
      if (!lastTime) lastTime = time;
      const delta = time - lastTime;
      lastTime = time;
      ctx.clearRect(0, 0, width, height);
      drawStars();

      if (!prefersReduced.matches) {
        for (const star of stars) {
          star.a += star.dir * star.tw * delta * 0.06;
          if (star.a > 0.85 || star.a < 0.2) {
            star.dir *= -1;
          }
        }
        rafId = window.requestAnimationFrame(render);
      }
    };

    const resize = () => {
      const rect = canvas.getBoundingClientRect();
      width = Math.max(1, rect.width);
      height = Math.max(1, rect.height);
      const dpr = window.devicePixelRatio || 1;
      canvas.width = Math.round(width * dpr);
      canvas.height = Math.round(height * dpr);
      ctx.setTransform(dpr, 0, 0, dpr, 0, 0);
      render(0);
    };

    resize();
    if (!prefersReduced.matches) {
      rafId = window.requestAnimationFrame(render);
    }

    window.addEventListener("resize", resize);

    return () => {
      window.removeEventListener("resize", resize);
      window.cancelAnimationFrame(rafId);
    };
  }, []);

  return <canvas ref={canvasRef} className={className} aria-hidden="true" />;
}
